/* Deƒüi≈ükenler App.css‚Äôten:
   --color-bg (siyah), --color-text (beyaz), --color-accent (kƒ±rmƒ±zƒ±), --color-border, --color-shadow
*/

/* Grid d√ºzeni */
.pg-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.4rem;
}

/* Kart ‚Äì normal durum */
.pg-card {
  position: relative;
  border: 1px solid var(--color-border);
  border-radius: 12px;
  padding: 1.1rem 1.1rem 1rem;
  background: rgba(255,255,255,0.06);          /* koyu cam */
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  color: var(--color-text);
  box-shadow: 0 0 12px rgba(0,0,0,0.25);
  transition: background .25s ease, color .25s ease,
              border-color .25s ease, transform .18s ease, box-shadow .25s ease;
}

/* Ba≈ülƒ±k satƒ±rƒ± */
.pg-head {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  margin-bottom: 0.6rem;
}

.pg-icon {
  font-size: 1.35rem;
  filter: drop-shadow(0 0 4px var(--color-shadow));
}

.pg-title {
  font-size: 1.05rem;
  margin: 0;
  letter-spacing: .2px;
}

/* A√ßƒ±klama */
.pg-desc {
  margin: 0 0 .8rem 0;
  font-size: .95rem;
  line-height: 1.5;
  opacity: .95;
}

/* Tag‚Äôler ‚Äì mini kutular (kategori) ‚Äì normalde transparan ve kƒ±rmƒ±zƒ± yazƒ±lƒ± */
.pg-tags {
  display: flex;
  flex-wrap: wrap;
  gap: .45rem;
  margin-bottom: .9rem;
}

.pg-tag {
  padding: .28rem .6rem;
  font-size: .8rem;
  border-radius: 999px;
  border: 1px solid var(--color-accent);
  color: var(--color-accent);
  background: transparent;                     /* normalde beyaz deƒüil */
  transition: background .25s ease, color .25s ease, border-color .25s ease;
  white-space: nowrap;
}

/* Link */
.pg-link {
  display: inline-block;
  font-weight: 700;
  text-decoration: none;
  color: var(--color-accent);
  border-bottom: 1px dashed var(--color-accent);
  transition: color .25s ease, border-color .25s ease, transform .18s ease;
}
.pg-link:hover {
  color: #fff;
  border-color: #fff;
  transform: translateY(-1px);
}

/* === HOVER KURALI (ƒ∞stediƒüin davranƒ±≈ü) ===
   - Kart kƒ±rmƒ±zƒ± olur
   - Metinler siyah olur
   - Mini kutular (tag‚Äôler) beyaz olur, yazƒ± siyah
*/
.pg-card:hover {
  background: var(--color-accent);             /* kart kƒ±rmƒ±zƒ± */
  color: #000;                                  /* yazƒ±lar siyah */
  border-color: var(--color-accent);
  transform: translateY(-3px);
  box-shadow:
    0 8px 18px rgba(255,0,60,0.25),
    0 0 22px rgba(255,0,60,0.35);
}

/* Hover‚Äôda ba≈ülƒ±k ve a√ßƒ±klama da siyah kalsƒ±n */
.pg-card:hover .pg-title,
.pg-card:hover .pg-desc {
  color: #000;
}

/* Hover‚Äôda icon siyaha yakƒ±n g√∂r√ºns√ºn */
.pg-card:hover .pg-icon {
  filter: drop-shadow(0 0 0 rgba(0,0,0,0));
  color: #000;
}

/* Hover‚Äôda mini kutular (kategori tag‚Äôleri) beyaz olmalƒ± */
.pg-card:hover .pg-tag {
  background: #fff;          /* BEYAZ */
  color: #000;               /* siyah yazƒ± */
  border-color: #fff;        /* beyaz √ßer√ßeve */
}

/* Hover‚Äôda link kontrastƒ± */
.pg-card:hover .pg-link {
  color: #000;
  border-color: #000;
}

/* Responsive ufak dokunu≈ülar */
@media (max-width: 480px) {
  .pg-grid { gap: 1rem; }
  .pg-card { padding: .95rem; }
  .pg-title { font-size: 1rem; }
  .pg-desc { font-size: .9rem; }
  .pg-tag { font-size: .75rem; padding: .22rem .5rem; }
}
